<template>
  <b-container fluid>
    <b-row>
      <b-col sm="12">
        <iq-card>
          <tab-nav :pills="true" id="pills-tab" class="mb-3 p-3">
            <tab-nav-items
              :active="true"
              id="pills-home-tab"
              href="#pills-home"
              ariaControls="pills-home"
              role="tab"
              :ariaSelected="true"
              title="Open Ticket"
            />
            <tab-nav-items
              :active="false"
              id="pills-closed-ticket-tab"
              href="#pills-closed-ticket"
              ariaControls="pills-closed-ticket"
              role="tab"
              :ariaSelected="false"
              title="Closed Ticket"
            />
            <tab-nav-items
              :active="false"
              id="pills-add-ticket-tab"
              href="#pills-add-ticket"
              ariaControls="pills-add-ticket"
              role="tab"
              :ariaSelected="false"
              title="Add New Ticket"
            />
          </tab-nav>
          <tab-content id="pills-tabContent-2" class="px-3 pb-3">
            <tab-content-item
              :active="true"
              id="pills-home"
              aria-labelled-by="pills-home-tab"
            >
              <b-table-simple responsive>
                <b-thead>
                  <b-th>#ID</b-th>
                  <b-th class="w-75">Title</b-th>
                  <b-th>Status</b-th>
                  <b-th>Category</b-th>
                  <b-th>Created At</b-th>
                </b-thead>
                <b-tbody>
                  <b-tr v-for="i in 3" :key="i">
                    <b-th>{{ i }}</b-th>
                    <b-td
                      ><router-link tag="a" to="/customer/ticket-detail/1">Deposit</router-link></b-td
                    >
                    <b-td><b-badge variant="success">Open</b-badge></b-td>
                    <b-td>Deposits</b-td>
                    <b-td>05-02-2021 12:47</b-td>
                  </b-tr>
                </b-tbody>
              </b-table-simple>
            </tab-content-item>
            <tab-content-item
              :active="false"
              id="pills-closed-ticket"
              aria-labelled-by="pills-closed-ticket-tab"
            >
              <b-table-simple responsive>
                <b-thead>
                  <b-th>#ID</b-th>
                  <b-th class="w-75">Title</b-th>
                  <b-th>Status</b-th>
                  <b-th>Category</b-th>
                  <b-th>Created At</b-th>
                </b-thead>
                <b-tbody>
                  <b-tr v-for="i in 7" :key="i">
                    <b-th>{{ i }}</b-th>
                    <b-td
                      ><router-link tag="a" to="">Deposit</router-link></b-td
                    >
                    <b-td><b-badge variant="danger">Closed</b-badge></b-td>
                    <b-td>Deposits</b-td>
                    <b-td>05-02-2021 12:47</b-td>
                  </b-tr>
                </b-tbody>
              </b-table-simple>
            </tab-content-item>
            <tab-content-item
              :active="false"
              id="pills-add-ticket"
              aria-labelled-by="pills-add-ticket-tab"
            >
              <b-form>
                <b-form-group
                  label-for="exampleFormControlSelect1"
                  label="Category"
                >
                  <b-form-select
                    plain
                    v-model="ticket.category"
                    :options="options1"
                    id="exampleFormControlSelect1"
                  >
                    <template v-slot:first>
                      <b-form-select-option :value="null" disabled
                        >Select category</b-form-select-option
                      >
                    </template>
                  </b-form-select>
                </b-form-group>
                <b-form-group label-for="exampleInputPlaceholder" label="Title">
                  <b-form-input
                    type="text"
                    id="exampleInputPlaceholder"
                    placeholder="Title"
                    v-model="ticket.title"
                  ></b-form-input>
                </b-form-group>
                <b-form-group
                  label="Example textarea"
                  label-for="exampleFormControlTextarea1"
                >
                  <b-form-textarea
                    id="exampleFormControlTextarea1"
                    rows="5"
                  ></b-form-textarea>
                </b-form-group>
                <b-form-group label-for="customFile">
                  <b-form-file
                    placeholder="Choose a file"
                    id="customFile"
                  ></b-form-file>
                </b-form-group>
                <b-button type="submit" variant="primary">Submit</b-button>
                <b-button type="submit" variant="none" class="iq-bg-danger ml-3"
                  >Cancel</b-button
                >
              </b-form>
            </tab-content-item>
          </tab-content>
        </iq-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  data () {
    return {
      ticket: {
        category: '',
        title: '',
        description: '',
        file: ''
      },
      options1: [
        { value: 'Withdrawals', text: 'Withdrawals' },
        { value: 'Deposits', text: 'Deposits' },
        { value: 'Verification', text: 'Verification' },
        { value: 'Accounts', text: 'Accounts' },
        { value: 'Other', text: 'Other' }
      ]
    }
  }
}
</script>

<style>
</style>
